.home{
   position: relative; 
   height: 80vh;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;

   &__text-wrapper{
    display: flex;
    justify-content: center;
    align-items: center; 
    height: 100%;
   }

    h1{
        color:var(--blue-theme-main-color);
        margin:0px;
        font-weight: 500;
        font-size: 8rem;
        line-height: 10rem;
        position: relative;
        overflow: hidden;
        white-space: nowrap;
        animation: typingEffectAnimation 2.5s steps(30, end)
    }
}

@media only screen and (max-width : 550px){
   .home__text-wrapper h1{
    font-size: 3rem; 
    line-height: 5rem;
   }
}

@media only screen and (min-width:551px) and (max-width : 768px){
    .home__text-wrapper h1{
        font-size: 4rem; 
        line-height: 6rem;
       }
}
@media only screen and (min-width:769px) and (max-width : 990px){
    .home__text-wrapper h1{
        font-size: 6rem; 
        line-height: 8rem;
       }
}

@keyframes typingEffectAnimation {
    from {
        width:0; 
    }
    to {
        width:100%
    }
}