.skills{
    &__content-wrapper{
        display: flex; 
        padding: 20px; 
        gap: 20px; 

        &__inner-content{
            flex:1 ; 
            display:flex; 
            flex-direction: column;
            align-items: center;

            &__category-text{
             display: inline-block;
             color: var(--blue-theme-main-color);
             font-size: 15px;
             font-weight: 700;
             line-height: 1rem;
             letter-spacing: 0.5px;
             position: relative;
             text-transform: uppercase;
             padding: 5px 10px;
             border-bottom: 2px solid;
            }
            &__category-text::before, &__category-text::after{
               position: absolute;
               content: ''; 
               width: 2px;
               height: 5px;
               background-color: var(--blue-theme-main-color);
               bottom: -1px;
            }
            &__category-text::before{
                left:0;
            }
            &__category-text::after{
                right: 0;
            }
            div{
                width: 100%;
            }
            .progressbar-wrapper{
                padding:50p 0 px 0 px 0 px;
                p{
                    font-size: 1.6rem;
                    margin: 0 0 10px; 
                    font-weight: 500;
                    text-transform: uppercase;
                    color: var(--blue-theme-sub-text-color);
                }
            }
        }
    }
}

@media only screen and (max-width:768px){
    .skills{
        &__content-wrapper{
            flex-direction:column; 

        }
    }
    .skills__content-wrapper__inner-content__progressbar-container{
        margin-bottom: 20x;
    }
    
}