.wrapper{
    display:flex;
    align-items: center;
    padding: 20px;
    border-bottom: 1px dashed var(--blue-theme-main-color);

    h2{
       font-size: 36px;
       font-weight: 700;
       letter-spacing: 1.5px; 
       line-height: capitalize;
       color:var(--blue-theme-main-color);
       display: flex;
       flex-grow: 1;
    }

    span{
        svg{
            color:var(--blue-theme-main-color);
        }
    }
}